}

.chart-card h3 {
  color: #111827;
  font-size: 1.125rem;
  font-weight: 600;
  margin: 0 0 1.5rem 0;
}

.no-data-indicator {
  background: rgba(255, 193, 7, 0.1);
  border: 1px solid rgba(255, 193, 7, 0.3);
  color: #fbbf24;
  padding: 0.75rem;
  border-radius: 8px;
  margin-bottom: 1rem;
  text-align: center;
  font-size: 0.875rem;
  font-weight: 500;
}

.sample-note {
  color: #6b7280 !important;
  font-size: 0.875rem;
  font-style: italic;
}

/* Revenue Trend Chart */
.revenue-trend {
  grid-column: span 2;
}

.line-chart .chart-container {
  display: flex;
  align-items: end;
  gap: 0.25rem;
  height: 200px;
  padding: 1rem 0;
  border-bottom: 1px solid #e5e7eb;
  overflow-x: auto;
  overflow-y: visible;
  scroll-behavior: smooth;
}

.trend-bar {
  flex: 0 0 auto;
  min-width: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.trend-line {
  width: 100%;
  background: linear-gradient(45deg, #8b5cf6, #06b6d4);
  border-radius: 4px 4px 0 0;
  min-height: 2px;
  transition: all 0.5s ease;
  position: relative;
  cursor: pointer;
}

.trend-line:hover::before {
  content: attr(data-tooltip);
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 0.5rem;
  border-radius: 4px;
  font-size: 0.75rem;
  white-space: nowrap;
  z-index: 10;
}

.trend-label {
  color: #6b7280;
  font-size: 0.625rem;
  margin-top: 0.5rem;
  transform: rotate(-45deg);
  font-weight: 500;
  white-space: nowrap;
  text-align: center;
}

.chart-summary {
  margin-top: 1rem;
  text-align: center;
  color: #10b981;
  font-weight: 600;
  font-size: 1.125rem;
}

/* Revenue Distribution Pie Chart */
.pie-chart {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}

.pie-container {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  position: relative;
  background: #f8fafc;
  border: 2px solid #e5e7eb;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pie-container svg {
  width: 100%;
  height: 100%;
}

.pie-container circle {
  transition: stroke-dasharray 0.8s ease, stroke-dashoffset 0.8s ease;
}

.pie-legend {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
}

.legend-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 0.75rem;
  background: #f8fafc;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
}

.legend-color {
  width: 16px;
  height: 16px;
  border-radius: 4px;
}

.room-color {
  background: #8b5cf6;
}

.food-color {
  background: #06b6d4;
}

.laundry-color {
  background: #f59e0b;
}

.legend-item span {
  color: #111827;
  font-weight: 500;
}

.percentage {
  color: #6b7280 !important;
  margin-left: auto;
  font-size: 0.875rem;
}

/* Room Occupancy Bar Chart */
.bar-chart-container {
  overflow-x: auto;
  overflow-y: hidden;
  padding: 0.5rem 0;
}

.bar-chart {
  display: flex;
  gap: 1rem;
  align-items: end;
  height: 200px;
  padding: 1rem 0;
}

.occupancy-bar {
  flex: 1 0 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

.bar-container {
  flex: 1;
  width: 100%;
  background: #f3f4f6;
  border-radius: 8px;
  position: relative;
  display: flex;
  align-items: end;
  min-height: 120px;
}

.occupancy-fill {
  width: 100%;
  background: linear-gradient(45deg, #3b82f6, #1d4ed8);
  border-radius: 0 0 8px 8px;
  transition: all 0.5s ease;
  position: relative;
}

.occupancy-percentage {
  position: absolute;
  top: -1.5rem;
  left: 50%;
  transform: translateX(-50%);
  color: #111827;
  font-size: 0.75rem;
  font-weight: 600;
}

.room-label {
  color: #111827;
  font-size: 0.75rem;
  font-weight: 600;
  margin-top: 0.5rem;
}

.booking-count {
  color: #6b7280;
  font-size: 0.625rem;
  margin-top: 0.25rem;
}

/* Top Items Chart */
.items-chart {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.item-bar {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding: 1rem;
  background: #f8fafc;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.item-bar:hover {
  background: #f1f5f9;
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.item-info {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.item-rank {
  background: linear-gradient(135deg, #8b5cf6 0%, #06b6d4 100%);
  color: #ffffff;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 600;
}

.item-name {
  color: #111827;
  font-weight: 600;
  flex: 1;
}

.item-stats {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.item-revenue {
  color: #10b981;
  font-weight: 600;
  font-size: 1.125rem;
}

.item-quantity {
  color: #6b7280;
  font-size: 0.875rem;
}

.item-progress {
  height: 6px;
  background: #e5e7eb;
  border-radius: 3px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #8b5cf6, #06b6d4);
  border-radius: 3px;
  transition: width 0.8s ease;
}

/* Responsive Design for Charts */
@media (max-width: 1024px) {
  .charts-grid {
    grid-template-columns: 1fr;
  }
  
  .revenue-trend {
    grid-column: span 1;
  }
  
  .charts-header {
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
  }
}

@media (max-width: 768px) {
  .admin-charts-section {
    padding: 0 1rem;
  }
  
  .chart-card {
    padding: 1rem;
  }
  
  .bar-chart {
    gap: 0.5rem;
  }
  
  .trend-label {
    font-size: 0.625rem;
  }
  
  .pie-container {
    width: 120px;
    height: 120px;
  }
}

/* Modern Dashboard Styles */
.dashboard {
  max-width: 1400px;
  margin: 0 auto;
  padding: 2rem;
  min-height: calc(100vh - 100px);
  background: #FAFAFA;
  color: #333333;
}

/* Stats Section */
.stats-section {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.stat-card {
  background: linear-gradient(135deg, #1e293b 0%, #334155 100%);
  border-radius: 16px;
  padding: 1.5rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.stat-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, #8b5cf6, #06b6d4);
}

.stat-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 35px rgba(0, 0, 0, 0.3);
  border-color: rgba(139, 92, 246, 0.3);
}

.stat-card:nth-child(1)::before {
  background: linear-gradient(90deg, #8b5cf6, #a855f7);
}

.stat-card:nth-child(2)::before {
  background: linear-gradient(90deg, #06b6d4, #0891b2);
}

.stat-card:nth-child(3)::before {
  background: linear-gradient(90deg, #10b981, #059669);
}

.stat-card:nth-child(4)::before {
  background: linear-gradient(90deg, #f59e0b, #d97706);
}

.stat-icon {
  font-size: 2rem;
  margin-bottom: 1rem;
  display: block;
}

.stat-content {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.stat-number {
  font-size: 2rem;
  font-weight: 700;
  color: #f1f5f9;
  line-height: 1;
}

.stat-label {
  font-size: 0.875rem;
  color: #94a3b8;
  font-weight: 500;
}

.stat-trend {
  font-size: 0.75rem;
  font-weight: 500;
  margin-top: 0.5rem;
}

.stat-trend.positive {
  color: #10b981;
}

.stat-trend.negative {
  color: #ef4444;
}

.dashboard-header {
  text-align: center;
  margin-bottom: 3rem;
  padding: 2rem;
  background: linear-gradient(135deg, #1a1f36 0%, #252a41 100%);
  border-radius: 24px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
  overflow: hidden;
}

.dashboard-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, #8b5cf6, #06b6d4, #10b981);
}

.dashboard-header h1 {
  font-size: 2.5rem;
  font-weight: 700;
  margin: 0 0 0.5rem 0;
  background: linear-gradient(135deg, #8b5cf6, #06b6d4);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.dashboard-subtitle {
  font-size: 1.1rem;
  color: #94a3b8;
  margin: 0;
  font-weight: 400;
}

.dashboard-content {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.welcome-section {
  text-align: center;
  padding: 2rem;
  background: linear-gradient(135deg, #1e293b 0%, #334155 100%);
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.welcome-section h2 {
  font-size: 1.8rem;
  color: #f1f5f9;
  margin: 0 0 1rem 0;
  font-weight: 600;
}

.welcome-section p {
  font-size: 1rem;
  color: #94a3b8;
  margin: 0;
}

.dashboard-actions {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 1.5rem;
  margin-top: 2rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.action-card {
  background: linear-gradient(135deg, #1e293b 0%, #334155 100%);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.action-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(139, 92, 246, 0.3), transparent);
}

.action-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  border-color: rgba(139, 92, 246, 0.3);
}

.action-card h3 {
  font-size: 1.3rem;
  color: #f1f5f9;
  margin: 0 0 1rem 0;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.action-card p {
  color: #94a3b8;
  margin: 0 0 2rem 0;
  font-size: 0.95rem;
  line-height: 1.6;
}

.dashboard-btn {
  width: 100%;
  padding: 1rem 1.5rem;
  border: none;
  border-radius: 12px;
  font-size: 0.95rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: none;
  letter-spacing: 0.3px;
  position: relative;
  overflow: hidden;
}

.dashboard-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  transition: left 0.5s ease;
}

.dashboard-btn:hover::before {
  left: 100%;
}

.dashboard-btn.primary {
  background: linear-gradient(135deg, #8b5cf6, #7c3aed);
  color: white;
  box-shadow: 0 4px 15px rgba(139, 92, 246, 0.3);
}

.dashboard-btn.primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(139, 92, 246, 0.4);
  background: linear-gradient(135deg, #7c3aed, #6d28d9);
}

.dashboard-btn.secondary {
  background: linear-gradient(135deg, #06b6d4, #0891b2);
  color: white;
  box-shadow: 0 4px 15px rgba(6, 182, 212, 0.3);
}

.dashboard-btn.secondary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(6, 182, 212, 0.4);
  background: linear-gradient(135deg, #0891b2, #0e7490);
}

@media (max-width: 768px) {
  .dashboard {
    padding: 1rem;
  }
  
  .dashboard-header h1 {
    font-size: 2rem;
  }
  
  .stats-section {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
  
  .dashboard-actions {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    max-width: 100%;
  }
  
  .action-card {
    padding: 1.5rem;
  }
}

/* PDF Creator Styles */
.pdf-creator {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
}

.pdf-creator h2 {
  color: #2d3748;
  margin-bottom: 1rem;
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
}

.pdf-creator > p {
  text-align: center;
  color: #718096;
  margin-bottom: 2rem;
  font-size: 1.1rem;
}

.pdf-form {
  margin-bottom: 2rem;
}

.pdf-form .form-group {
  margin-bottom: 1.5rem;
}

.pdf-form label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: #2d3748;
  font-size: 1rem;
}

.pdf-form select {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid #e2e8f0;
  border-radius: 8px;
  font-size: 1rem;
  background: white;
  color: #2d3748;
  transition: all 0.3s ease;
}

.pdf-form select:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.pdf-form select:disabled {
  background: #f7fafc;
  color: #a0aec0;
  cursor: not-allowed;
}

.pdf-description {
  background: #f7fafc;
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1.5rem;
  border-left: 4px solid #667eea;
}

.pdf-description p {
  margin: 0;
  color: #4a5568;
  font-size: 0.95rem;
  line-height: 1.5;
}

.create-pdf-btn {
  width: 100%;
  padding: 1rem;
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.create-pdf-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.3);
}

.create-pdf-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.message {
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 500;
}

.message.success {
  background: #f0fff4;
  border: 1px solid #9ae6b4;
  color: #22543d;
}

.message.error {
  background: #fed7d7;
  border: 1px solid #feb2b2;
  color: #742a2a;
}

.close-btn {
  background: none;
  border: none;
  font-size: 1.2rem;
  cursor: pointer;
  color: inherit;
  padding: 0;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: background 0.2s ease;
}

.close-btn:hover {
  background: rgba(0, 0, 0, 0.1);
}

.pdf-info {
  background: #f7fafc;
  padding: 1.5rem;
  border-radius: 8px;
  border: 1px solid #e2e8f0;
}

.pdf-info h3 {
  color: #2d3748;
  margin-bottom: 1rem;
  font-size: 1.2rem;
  font-weight: 600;
}

.pdf-info ul {
  list-style: none;
  padding: 0;
  margin-bottom: 1rem;
}

.pdf-info li {
  padding: 0.5rem 0;
  border-bottom: 1px solid #e2e8f0;
  color: #4a5568;
  line-height: 1.5;
}

.pdf-info li:last-child {
  border-bottom: none;
}

.pdf-info li strong {
  color: #2d3748;
  font-weight: 600;
}

.pdf-info p {
  margin: 0;
  color: #718096;
  font-size: 0.9rem;
  font-style: italic;
}

/* Dashboard button for PDF Creator */
.dashboard-btn.tertiary {
  background: linear-gradient(135deg, #ed8936, #dd6b20);
}

.dashboard-btn.tertiary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(237, 137, 54, 0.3);
}

/* Action buttons in table */
.action-buttons {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  align-items: center;
}

.print-btn {
  background: linear-gradient(135deg, #3182ce, #2c5aa0);
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.25rem;
  min-width: 80px;
  justify-content: center;
}

.print-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(49, 130, 206, 0.3);
  background: linear-gradient(135deg, #2c5aa0, #1e40af);
}

.print-btn:active {
  transform: translateY(0);
  box-shadow: 0 2px 6px rgba(49, 130, 206, 0.3);
}

/* Table action column styling */
.table-cell.action {
  text-align: center;
  padding: 0.5rem;
}

.table-cell.action .action-buttons {
  margin: 0;
}

/* Demo buttons removed for security reasons */

/* Loading Overlay */
.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(5px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: inherit;
  z-index: 1000;
}

.loading-spinner-elegant {
