  50% { transform: scale(1.1); opacity: 0.7; }
}

.loading-text {
  color: rgba(255, 255, 255, 0.9);
  margin-top: 15px;
  font-size: 16px;
  font-weight: 300;
  letter-spacing: 0.5px;
}




