  font-weight: 700;
  color: #1e3a8a;
}

.card-header p {
  margin: 0;
  color: #64748b;
  font-size: 16px;
}

.form-content {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.form-group {
