}

.description-content h4 {
  margin: 0 0 8px 0;
  color: #1e3a8a;
  font-size: 16px;
  font-weight: 600;
}

.description-content p {
  margin: 0;
  color: #374151;
  line-height: 1.6;
}

/* Invoice Selection Section */
.invoice-selection-section {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Invoice Preview Card */
.invoice-preview-card {
