  font-weight: 600;
}

.preview-content {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.preview-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.preview-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  background: white;
  border-radius: 10px;
  border: 1px solid #e5e7eb;
}

.preview-label {
  font-weight: 600;
  color: #374151;
  font-size: 14px;
}

.preview-value {
  color: #1f2937;
  font-size: 14px;
}

.preview-value.amount {
  font-weight: 700;
  color: #059669;
  font-size: 16px;
}

/* No Invoices Card */
.no-invoices-card {
