  border-radius: 15px;
  border: 2px solid #fecaca;
}

.no-invoices-icon {
  font-size: 48px;
}

.no-invoices-content h4 {
  margin: 0 0 8px 0;
  color: #dc2626;
  font-size: 18px;
  font-weight: 600;
}

.no-invoices-content p {
  margin: 0 0 15px 0;
  color: #7f1d1d;
}

.create-invoice-btn {
  padding: 10px 20px;
  background: linear-gradient(135deg, #dc2626, #b91c1c);
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.create-invoice-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(220, 38, 38, 0.3);
}

/* Advanced Options */
.advanced-options-card {
  background: linear-gradient(135deg, #f0f9ff, #e0f2fe);
  border-radius: 15px;
  padding: 25px;
  border: 2px solid #bae6fd;
}

.advanced-header {
  margin-bottom: 20px;
  text-align: center;
}

.advanced-header h4 {
  margin: 0 0 5px 0;
  color: #0c4a6e;
  font-size: 18px;
  font-weight: 600;
}

.advanced-header p {
  margin: 0;
  color: #0369a1;
  font-size: 14px;
}

.advanced-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.setting-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.setting-label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 500;
  color: #0c4a6e;
  font-size: 14px;
  cursor: pointer;
}

.setting-label input[type="checkbox"] {
  width: 18px;
  height: 18px;
