@media (max-width: 768px) {
  .login-grid {
    grid-template-columns: 1fr;
    height: auto;
  }
  
  .login-left-column {
    grid-column: span 1;
    min-height: 200px;
  }
  
  .login-right-column {
    grid-column: span 1;
    padding: 20px;
  }
  
  .login-hotel-name {
    font-size: 24px;
  }
  
  .login-hotel-tagline {
    font-size: 16px;
  }
  
  .login-title {
    font-size: 24px;
  }
