  color: white;\r\n  box-shadow: 0 4px 15px rgba(139, 92, 246, 0.3);\r\n}\r\n\r\n.dashboard-btn.primary:hover {\r\n  transform: translateY(-2px);\r\n  box-shadow: 0 8px 25px rgba(139, 92, 246, 0.4);\r\n  background: linear-gradient(135deg, #7c3aed, #6d28d9);\r\n}\r\n\r\n.dashboard-btn.secondary {\r\n  background: linear-gradient(135deg, #06b6d4, #0891b2);\r\n  color: white;\r\n  box-shadow: 0 4px 15px rgba(6, 182, 212, 0.3);\r\n}\r\n\r\n.dashboard-btn.secondary:hover {\r\n  transform: translateY(-2px);\r\n  box-shadow: 0 8px 25px rgba(6, 182, 212, 0.4);\r\n  background: linear-gradient(135deg, #0891b2, #0e7490);\r\n}\r\n\r\n@media (max-width: 768px) {\r\n  .dashboard {\r\n    padding: 1rem;\r\n  }\r\n  \r\n  .dashboard-header h1 {\r\n    font-size: 2rem;\r\n  }\r\n  \r\n  .stats-section {\r\n    grid-template-columns: repeat(2, 1fr);\r\n    gap: 1rem;\r\n  }\r\n  \r\n  .dashboard-actions {\r\n    grid-template-columns: 1fr;\r\n    grid-template-rows: auto;\r\n    max-width: 100%;\r\n  }\r\n  \r\n  .action-card {\r\n    padding: 1.5rem;\r\n  }\r\n}\r\n\r\n/* PDF Creator Styles */\r\n.pdf-creator {\r\n  max-width: 800px;\r\n  margin: 0 auto;\r\n  padding: 2rem;\r\n  background: rgba(255, 255, 255, 0.95);\r\n  border-radius: 12px;\r\n  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);\r\n  backdrop-filter: blur(10px);\r\n}\r\n\r\n.pdf-creator h2 {\r\n  color: #2d3748;\r\n  margin-bottom: 1rem;\r\n  text-align: center;\r\n  font-size: 2rem;\r\n  font-weight: 700;\r\n}\r\n\r\n.pdf-creator > p {\r\n  text-align: center;\r\n  color: #718096;\r\n  margin-bottom: 2rem;\r\n  font-size: 1.1rem;\r\n}\r\n\r\n.pdf-form {\r\n  margin-bottom: 2rem;\r\n}\r\n\r\n.pdf-form .form-group {\r\n  margin-bottom: 1.5rem;\r\n}\r\n\r\n.pdf-form label {\r\n  display: block;\r\n  margin-bottom: 0.5rem;\r\n  font-weight: 600;\r\n  color: #2d3748;\r\n  font-size: 1rem;\r\n}\r\n\r\n.pdf-form select {\r\n  width: 100%;\r\n  padding: 0.75rem;\r\n  border: 2px solid #e2e8f0;\r\n  border-radius: 8px;\r\n  font-size: 1rem;\r\n  background: white;\r\n  color: #2d3748;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.pdf-form select:focus {\r\n  outline: none;\r\n  border-color: #667eea;\r\n  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);\r\n}\r\n\r\n.pdf-form select:disabled {\r\n  background: #f7fafc;\r\n  color: #a0aec0;\r\n  cursor: not-allowed;\r\n}\r\n\r\n.pdf-description {\r\n  background: #f7fafc;\r\n  padding: 1rem;\r\n  border-radius: 8px;\r\n  margin-bottom: 1.5rem;\r\n  border-left: 4px solid #667eea;\r\n}\r\n\r\n.pdf-description p {\r\n  margin: 0;\r\n  color: #4a5568;\r\n  font-size: 0.95rem;\r\n  line-height: 1.5;\r\n}\r\n\r\n.create-pdf-btn {\r\n  width: 100%;\r\n  padding: 1rem;\r\n  background: linear-gradient(135deg, #667eea, #764ba2);\r\n  color: white;\r\n  border: none;\r\n  border-radius: 8px;\r\n  font-size: 1.1rem;\r\n  font-weight: 600;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  gap: 0.5rem;\r\n}\r\n\r\n.create-pdf-btn:hover:not(:disabled) {\r\n  transform: translateY(-2px);\r\n  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.3);\r\n}\r\n\r\n.create-pdf-btn:disabled {\r\n  opacity: 0.6;\r\n  cursor: not-allowed;\r\n  transform: none;\r\n}\r\n\r\n.message {\r\n  padding: 1rem;\r\n  border-radius: 8px;\r\n  margin-bottom: 1.5rem;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n  font-weight: 500;\r\n}\r\n\r\n.message.success {\r\n  background: #f0fff4;\r\n  border: 1px solid #9ae6b4;\r\n  color: #22543d;\r\n}\r\n\r\n.message.error {\r\n  background: #fed7d7;\r\n  border: 1px solid #feb2b2;\r\n  color: #742a2a;\r\n}\r\n\r\n.close-btn {\r\n  background: none;\r\n  border: none;\r\n  font-size: 1.2rem;\r\n  cursor: pointer;\r\n  color: inherit;\r\n  padding: 0;\r\n  width: 20px;\r\n  height: 20px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  border-radius: 50%;\r\n  transition: background 0.2s ease;\r\n}\r\n\r\n.close-btn:hover {\r\n  background: rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.pdf-info {\r\n  background: #f7fafc;\r\n  padding: 1.5rem;\r\n  border-radius: 8px;\r\n  border: 1px solid #e2e8f0;\r\n}\r\n\r\n.pdf-info h3 {\r\n  color: #2d3748;\r\n  margin-bottom: 1rem;\r\n  font-size: 1.2rem;\r\n  font-weight: 600;\r\n}\r\n\r\n.pdf-info ul {\r\n  list-style: none;\r\n  padding: 0;\r\n  margin-bottom: 1rem;\r\n}\r\n\r\n.pdf-info li {\r\n  padding: 0.5rem 0;\r\n  border-bottom: 1px solid #e2e8f0;\r\n  color: #4a5568;\r\n  line-height: 1.5;\r\n}\r\n\r\n.pdf-info li:last-child {\r\n  border-bottom: none;\r\n}\r\n\r\n.pdf-info li strong {\r\n  color: #2d3748;\r\n  font-weight: 600;\r\n}\r\n\r\n.pdf-info p {\r\n  margin: 0;\r\n  color: #718096;\r\n  font-size: 0.9rem;\r\n  font-style: italic;\r\n}\r\n\r\n/* Dashboard button for PDF Creator */\r\n.dashboard-btn.tertiary {\r\n  background: linear-gradient(135deg, #ed8936, #dd6b20);\r\n}\r\n\r\n.dashboard-btn.tertiary:hover {\r\n  transform: translateY(-2px);\r\n  box-shadow: 0 8px 25px rgba(237, 137, 54, 0.3);\r\n}\r\n\r\n/* Action buttons in table */\r\n.action-buttons {\r\n  display: flex;\r\n  gap: 0.5rem;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n\r\n.print-btn {\r\n  background: linear-gradient(135deg, #3182ce, #2c5aa0);\r\n  color: white;\r\n  border: none;\r\n  padding: 0.5rem 1rem;\r\n  border-radius: 6px;\r\n  font-size: 0.875rem;\r\n  font-weight: 500;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 0.25rem;\r\n  min-width: 80px;\r\n  justify-content: center;\r\n}\r\n\r\n.print-btn:hover {\r\n  transform: translateY(-1px);\r\n  box-shadow: 0 4px 12px rgba(49, 130, 206, 0.3);\r\n  background: linear-gradient(135deg, #2c5aa0, #1e40af);\r\n}\r\n\r\n.print-btn:active {\r\n  transform: translateY(0);\r\n  box-shadow: 0 2px 6px rgba(49, 130, 206, 0.3);\r\n}\r\n\r\n/* Table action column styling */\r\n.table-cell.action {\r\n  text-align: center;\r\n  padding: 0.5rem;\r\n}\r\n\r\n.table-cell.action .action-buttons {\r\n  margin: 0;\r\n}\r\n\r\n/* Demo buttons removed for security reasons */\r\n\r\n/* Loading Overlay */\r\n.loading-overlay {\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  background: rgba(0, 0, 0, 0.7);\r\n  backdrop-filter: blur(5px);\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  border-radius: inherit;\r\n  z-index: 1000;\r\n}\r\n\r\n.loading-spinner-elegant {\r\n  font-size: 32px;\r\n  animation: pulse 1.5s ease-in-out infinite;\r\n}\r\n\r\n@keyframes pulse {\r\n  0%, 100% { transform: scale(1); opacity: 1; }\r\n  50% { transform: scale(1.1); opacity: 0.7; }\r\n}\r\n\r\n.loading-text {\r\n  color: rgba(255, 255, 255, 0.9);\r\n  margin-top: 15px;\r\n  font-size: 16px;\r\n  font-weight: 300;\r\n  letter-spacing: 0.5px;\r\n}\r\n\r\n\r\n\r\n\r\n/* Enhanced PDF Creator Styles */\r\n.pdf-creator-enhanced {\r\n  min-height: 100vh;\r\n  background: linear-gradient(135deg, #1e3a8a 0%, #3b82f6 50%, #60a5fa 100%);\r\n  padding: 20px;\r\n  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;\r\n}\r\n\r\n/* Header Section */\r\n.pdf-creator-header {\r\n  background: rgba(255, 255, 255, 0.95);\r\n  backdrop-filter: blur(20px);\r\n  border-radius: 20px;\r\n  padding: 30px;\r\n  margin-bottom: 30px;\r\n  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);\r\n  border: 1px solid rgba(255, 255, 255, 0.2);\r\n}\r\n\r\n.header-content {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n}\r\n\r\n.header-left {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 20px;\r\n}\r\n\r\n.hotel-logo {\r\n  font-size: 48px;\r\n  background: linear-gradient(135deg, #1e3a8a, #3b82f6);\r\n  -webkit-background-clip: text;\r\n  -webkit-text-fill-color: transparent;\r\n  background-clip: text;\r\n}\r\n\r\n.header-text h2 {\r\n  margin: 0;\r\n  font-size: 28px;\r\n  font-weight: 700;\r\n  color: #1e3a8a;\r\n  background: linear-gradient(135deg, #1e3a8a, #3b82f6);\r\n  -webkit-background-clip: text;\r\n  -webkit-text-fill-color: transparent;\r\n  background-clip: text;\r\n}\r\n\r\n.header-text p {\r\n  margin: 5px 0 0 0;\r\n  color: #64748b;\r\n  font-size: 16px;\r\n}\r\n\r\n.header-stats {\r\n  display: flex;\r\n  gap: 30px;\r\n}\r\n\r\n.stat-item {\r\n  text-align: center;\r\n  padding: 15px 25px;\r\n  background: linear-gradient(135deg, #1e3a8a, #3b82f6);\r\n  border-radius: 15px;\r\n  color: white;\r\n  min-width: 100px;\r\n}\r\n\r\n.stat-number {\r\n  display: block;\r\n  font-size: 24px;\r\n  font-weight: 700;\r\n  margin-bottom: 5px;\r\n}\r\n\r\n.stat-label {\r\n  font-size: 12px;\r\n  opacity: 0.9;\r\n  text-transform: uppercase;\r\n  letter-spacing: 0.5px;\r\n}\r\n\r\n/* Main Content */\r\n.pdf-creator-content {\r\n  display: grid;\r\n  grid-template-columns: 1fr 400px;\r\n  gap: 30px;\r\n  max-width: 1400px;\r\n  margin: 0 auto;\r\n}\r\n\r\n/* Form Section */\r\n.pdf-form-section {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 20px;\r\n}\r\n\r\n.form-card {\r\n  background: rgba(255, 255, 255, 0.95);\r\n  backdrop-filter: blur(20px);\r\n  border-radius: 20px;\r\n  padding: 30px;\r\n  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);\r\n  border: 1px solid rgba(255, 255, 255, 0.2);\r\n}\r\n\r\n.card-header {\r\n  margin-bottom: 30px;\r\n  text-align: center;\r\n}\r\n\r\n.card-header h3 {\r\n  margin: 0 0 10px 0;\r\n  font-size: 24px;\r\n  font-weight: 700;\r\n  color: #1e3a8a;\r\n}\r\n\r\n.card-header p {\r\n  margin: 0;\r\n  color: #64748b;\r\n  font-size: 16px;\r\n}\r\n\r\n.form-content {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 25px;\r\n}\r\n\r\n.form-row {\r\n  display: grid;\r\n  grid-template-columns: 1fr 1fr;\r\n  gap: 20px;\r\n}\r\n\r\n.form-group {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 8px;\r\n}\r\n\r\n.form-group label {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 8px;\r\n  font-weight: 600;\r\n  color: #374151;\r\n  font-size: 14px;\r\n}\r\n\r\n.label-icon {\r\n  font-size: 16px;\r\n}\r\n\r\n.enhanced-select {\r\n  padding: 12px 16px;\r\n  border: 2px solid #e5e7eb;\r\n  border-radius: 12px;\r\n  font-size: 14px;\r\n  background: white;\r\n  transition: all 0.3s ease;\r\n  cursor: pointer;\r\n}\r\n\r\n.enhanced-select:focus {\r\n  outline: none;\r\n  border-color: #3b82f6;\r\n  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);\r\n}\r\n\r\n.enhanced-select:disabled {\r\n  background: #f9fafb;\r\n  color: #9ca3af;\r\n  cursor: not-allowed;\r\n}\r\n\r\n.toggle-btn {\r\n  padding: 12px 20px;\r\n  background: linear-gradient(135deg, #3b82f6, #1e3a8a);\r\n  color: white;\r\n  border: none;\r\n  border-radius: 12px;\r\n  font-size: 14px;\r\n  font-weight: 600;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.toggle-btn:hover {\r\n  transform: translateY(-2px);\r\n  box-shadow: 0 8px 25px rgba(59, 130, 246, 0.3);\r\n}\r\n\r\n/* Description Card */\r\n.pdf-description-card {\r\n  display: flex;\r\n  align-items: flex-start;\r\n  gap: 15px;\r\n  padding: 20px;\r\n  background: linear-gradient(135deg, #eff6ff, #dbeafe);\r\n  border-radius: 15px;\r\n  border-left: 4px solid #3b82f6;\r\n}\r\n\r\n.description-icon {\r\n  font-size: 24px;\r\n  margin-top: 2px;\r\n}\r\n\r\n.description-content h4 {\r\n  margin: 0 0 8px 0;\r\n  color: #1e3a8a;\r\n  font-size: 16px;\r\n  font-weight: 600;\r\n}\r\n\r\n.description-content p {\r\n  margin: 0;\r\n  color: #374151;\r\n  line-height: 1.6;\r\n}\r\n\r\n/* Invoice Selection Section */\r\n.invoice-selection-section {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 20px;\r\n}\r\n\r\n/* Invoice Preview Card */\r\n.invoice-preview-card {\r\n  background: linear-gradient(135deg, #f8fafc, #f1f5f9);\r\n  border-radius: 15px;\r\n  padding: 25px;\r\n  border: 2px solid #e2e8f0;\r\n}\r\n\r\n.preview-header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.preview-header h4 {\r\n  margin: 0;\r\n  color: #1e3a8a;\r\n  font-size: 18px;\r\n  font-weight: 600;\r\n}\r\n\r\n.preview-badge {\r\n  background: linear-gradient(135deg, #10b981, #059669);\r\n  color: white;\r\n  padding: 4px 12px;\r\n  border-radius: 20px;\r\n  font-size: 12px;\r\n  font-weight: 600;\r\n}\r\n\r\n.preview-content {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 15px;\r\n}\r\n\r\n.preview-row {\r\n  display: grid;\r\n  grid-template-columns: 1fr 1fr;\r\n  gap: 20px;\r\n}\r\n\r\n.preview-item {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  padding: 12px 16px;\r\n  background: white;\r\n  border-radius: 10px;\r\n  border: 1px solid #e5e7eb;\r\n}\r\n\r\n.preview-label {\r\n  font-weight: 600;\r\n  color: #374151;\r\n  font-size: 14px;\r\n}\r\n\r\n.preview-value {\r\n  color: #1f2937;\r\n  font-size: 14px;\r\n}\r\n\r\n.preview-value.amount {\r\n  font-weight: 700;\r\n  color: #059669;\r\n  font-size: 16px;\r\n}\r\n\r\n/* No Invoices Card */\r\n.no-invoices-card {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 20px;\r\n  padding: 30px;\r\n  background: linear-gradient(135deg, #fef2f2, #fee2e2);\r\n  border-radius: 15px;\r\n  border: 2px solid #fecaca;\r\n}\r\n\r\n.no-invoices-icon {\r\n  font-size: 48px;\r\n}\r\n\r\n.no-invoices-content h4 {\r\n  margin: 0 0 8px 0;\r\n  color: #dc2626;\r\n  font-size: 18px;\r\n  font-weight: 600;\r\n}\r\n\r\n.no-invoices-content p {\r\n  margin: 0 0 15px 0;\r\n  color: #7f1d1d;\r\n}\r\n\r\n.create-invoice-btn {\r\n  padding: 10px 20px;\r\n  background: linear-gradient(135deg, #dc2626, #b91c1c);\r\n  color: white;\r\n  border: none;\r\n  border-radius: 10px;\r\n  font-size: 14px;\r\n  font-weight: 600;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.create-invoice-btn:hover {\r\n  transform: translateY(-2px);\r\n  box-shadow: 0 8px 25px rgba(220, 38, 38, 0.3);\r\n}\r\n\r\n/* Advanced Options */\r\n.advanced-options-card {\r\n  background: linear-gradient(135deg, #f0f9ff, #e0f2fe);\r\n  border-radius: 15px;\r\n  padding: 25px;\r\n  border: 2px solid #bae6fd;\r\n}\r\n\r\n.advanced-header {\r\n  margin-bottom: 20px;\r\n  text-align: center;\r\n}\r\n\r\n.advanced-header h4 {\r\n  margin: 0 0 5px 0;\r\n  color: #0c4a6e;\r\n  font-size: 18px;\r\n  font-weight: 600;\r\n}\r\n\r\n.advanced-header p {\r\n  margin: 0;\r\n  color: #0369a1;\r\n  font-size: 14px;\r\n}\r\n\r\n.advanced-grid {\r\n  display: grid;\r\n  grid-template-columns: 1fr 1fr;\r\n  gap: 20px;\r\n}\r\n\r\n.setting-group {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 8px;\r\n}\r\n\r\n.setting-label {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 8px;\r\n  font-weight: 500;\r\n  color: #0c4a6e;\r\n  font-size: 14px;\r\n  cursor: pointer;\r\n}\r\n\r\n.setting-label input[type=\"checkbox\"] {\r\n  width: 18px;\r\n  height: 18px;\r\n  accent-color: #0ea5e9;\r\n}\r\n\r\n.setting-select {\r\n  padding: 8px 12px;\r\n  border: 2px solid #bae6fd;\r\n  border-radius: 8px;\r\n  font-size: 14px;\r\n  background: white;\r\n  color: #0c4a6e;\r\n}\r\n\r\n/* Form Actions */\r\n.form-actions {\r\n  display: flex;\r\n  justify-content: center;\r\n  margin-top: 20px;\r\n}\r\n\r\n.generate-pdf-btn {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 12px;\r\n  padding: 16px 32px;\r\n  background: linear-gradient(135deg, #1e3a8a, #3b82f6);\r\n  color: white;\r\n  border: none;\r\n  border-radius: 15px;\r\n  font-size: 16px;\r\n  font-weight: 600;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n  box-shadow: 0 10px 30px rgba(30, 58, 138, 0.3);\r\n}\r\n\r\n.generate-pdf-btn:hover:not(:disabled) {\r\n  transform: translateY(-3px);\r\n  box-shadow: 0 15px 40px rgba(30, 58, 138, 0.4);\r\n}\r\n\r\n.generate-pdf-btn:disabled {\r\n  opacity: 0.6;\r\n  cursor: not-allowed;\r\n  transform: none;\r\n}\r\n\r\n.btn-icon {\r\n  font-size: 20px;\r\n}\r\n\r\n.loading-spinner {\r\n  width: 20px;\r\n  height: 20px;\r\n  border: 2px solid rgba(255, 255, 255, 0.3);\r\n  border-top: 2px solid white;\r\n  border-radius: 50%;\r\n  animation: spin 1s linear infinite;\r\n}\r\n\r\n@keyframes spin {\r\n  0% { transform: rotate(0deg); }\r\n  100% { transform: rotate(360deg); }\r\n}\r\n\r\n/* Info Section */\r\n.info-section {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 20px;\r\n}\r\n\r\n.info-card, .contact-info-card {\r\n  background: rgba(255, 255, 255, 0.95);\r\n  backdrop-filter: blur(20px);\r\n  border-radius: 20px;\r\n  padding: 30px;\r\n  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);\r\n  border: 1px solid rgba(255, 255, 255, 0.2);\r\n}\r\n\r\n.info-header, .contact-header {\r\n  margin-bottom: 25px;\r\n  text-align: center;\r\n}\r\n\r\n.info-header h3, .contact-header h3 {\r\n  margin: 0 0 8px 0;\r\n  font-size: 20px;\r\n  font-weight: 700;\r\n  color: #1e3a8a;\r\n}\r\n\r\n.info-header p, .contact-header p {\r\n  margin: 0;\r\n  color: #64748b;\r\n  font-size: 14px;\r\n}\r\n\r\n/* Features Grid */\r\n.features-grid {\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 20px;\r\n}\r\n\r\n.feature-item {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 15px;\r\n  padding: 20px;\r\n  background: linear-gradient(135deg, #f8fafc, #f1f5f9);\r\n  border-radius: 15px;\r\n  border: 1px solid #e2e8f0;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.feature-item:hover {\r\n  transform: translateY(-2px);\r\n  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.feature-icon {\r\n  font-size: 24px;\r\n  width: 50px;\r\n  height: 50px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background: linear-gradient(135deg, #1e3a8a, #3b82f6);\r\n  border-radius: 12px;\r\n  color: white;\r\n}\r\n\r\n.feature-item h4 {\r\n  margin: 0 0 5px 0;\r\n  color: #1e3a8a;\r\n  font-size: 16px;\r\n  font-weight: 600;\r\n}\r\n\r\n.feature-item p {\r\n  margin: 0;\r\n  color: #64748b;\r\n  font-size: 14px;\r\n  line-height: 1.5;\r\n}\r\n\r\n/* Contact Grid */\r\n.contact-grid {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 15px;\r\n}\r\n\r\n.contact-item {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 15px;\r\n  padding: 15px;\r\n  background: linear-gradient(135deg, #f0f9ff, #e0f2fe);\r\n  border-radius: 12px;\r\n  border: 1px solid #bae6fd;\r\n}\r\n\r\n.contact-icon {\r\n  font-size: 20px;\r\n  width: 40px;\r\n  height: 40px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background: linear-gradient(135deg, #0ea5e9, #0284c7);\r\n  border-radius: 10px;\r\n  color: white;\r\n}\r\n\r\n.contact-details strong {\r\n  display: block;\r\n  color: #0c4a6e;\r\n  font-size: 14px;\r\n  font-weight: 600;\r\n  margin-bottom: 2px;\r\n}\r\n\r\n.contact-details p {\r\n  margin: 0;\r\n  color: #0369a1;\r\n  font-size: 13px;\r\n}\r\n\r\n/* Message Overlay */\r\n.message-overlay {\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  background: rgba(0, 0, 0, 0.5);\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  z-index: 1000;\r\n  backdrop-filter: blur(5px);\r\n}\r\n\r\n.message-card {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 20px;\r\n  background: white;\r\n  padding: 30px;\r\n  border-radius: 20px;\r\n  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);\r\n  max-width: 500px;\r\n  width: 90%;\r\n  position: relative;\r\n}\r\n\r\n.message-icon {\r\n  font-size: 32px;\r\n  width: 60px;\r\n  height: 60px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  border-radius: 50%;\r\n  background: linear-gradient(135deg, #10b981, #059669);\r\n}\r\n\r\n.message-overlay.error .message-icon {\r\n  background: linear-gradient(135deg, #ef4444, #dc2626);\r\n}\r\n\r\n.message-content h4 {\r\n  margin: 0 0 8px 0;\r\n  color: #1f2937;\r\n  font-size: 18px;\r\n  font-weight: 600;\r\n}\r\n\r\n.message-content p {\r\n  margin: 0;\r\n  color: #6b7280;\r\n  line-height: 1.5;\r\n}\r\n\r\n.message-close-btn {\r\n  position: absolute;\r\n  top: 15px;\r\n  right: 15px;\r\n  background: none;\r\n  border: none;\r\n  font-size: 24px;\r\n  color: #9ca3af;\r\n  cursor: pointer;\r\n  padding: 5px;\r\n  border-radius: 50%;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.message-close-btn:hover {\r\n  background: #f3f4f6;\r\n  color: #374151;\r\n}\r\n\r\n/* Responsive Design */\r\n@media (max-width: 1200px) {\r\n  .pdf-creator-content {\r\n    grid-template-columns: 1fr;\r\n  }\r\n\r\n  .info-section {\r\n    order: -1;\r\n  }\r\n}\r\n\r\n@media (max-width: 768px) {\r\n  .pdf-creator-enhanced {\r\n    padding: 15px;\r\n  }\r\n\r\n  .header-content {\r\n    flex-direction: column;\r\n    gap: 20px;\r\n    text-align: center;\r\n  }\r\n\r\n  .header-stats {\r\n    justify-content: center;\r\n  }\r\n\r\n  .form-row {\r\n    grid-template-columns: 1fr;\r\n  }\r\n\r\n  .preview-row {\r\n    grid-template-columns: 1fr;\r\n  }\r\n\r\n  .advanced-grid {\r\n    grid-template-columns: 1fr;\r\n  }\r\n\r\n  .features-grid {\r\n    grid-template-columns: 1fr;\r\n  }\r\n\r\n  .message-card {\r\n    flex-direction: column;\r\n    text-align: center;\r\n  }\r\n  }\r\n\r\n/* PDF Viewer Styles */\r\n.pdf-viewer-overlay {\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  background: rgba(0, 0, 0, 0.8);\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  z-index: 10000;\r\n  backdrop-filter: blur(4px);\r\n}\r\n\r\n.pdf-viewer-container {\r\n  background: white;\r\n  border-radius: 12px;\r\n  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);\r\n  width: 90vw;\r\n  height: 90vh;\r\n  display: flex;\r\n  flex-direction: column;\r\n  overflow: hidden;\r\n}\r\n\r\n.pdf-viewer-header {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n  padding: 16px 24px;\r\n  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);\r\n  color: white;\r\n  border-radius: 12px 12px 0 0;\r\n}\r\n\r\n.pdf-viewer-title {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 8px;\r\n  font-size: 18px;\r\n  font-weight: 600;\r\n}\r\n\r\n.pdf-icon {\r\n  font-size: 20px;\r\n}\r\n\r\n.pdf-viewer-controls {\r\n  display: flex;\r\n  gap: 8px;\r\n}\r\n\r\n.pdf-viewer-close-btn {\r\n  background: rgba(255, 255, 255, 0.2);\r\n  border: none;\r\n  color: white;\r\n  width: 32px;\r\n  height: 32px;\r\n  border-radius: 6px;\r\n  cursor: pointer;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  font-size: 16px;\r\n  transition: all 0.2s ease;\r\n}\r\n\r\n.pdf-viewer-close-btn:hover {\r\n  background: rgba(255, 255, 255, 0.3);\r\n  transform: scale(1.05);\r\n}\r\n\r\n.pdf-viewer-content {\r\n  flex: 1;\r\n  position: relative;\r\n  background: #f8f9fa;\r\n}\r\n\r\n.pdf-webview {\r\n  width: 100%;\r\n  height: 100%;\r\n  border: none;\r\n}\r\n\r\n.pdf-loading {\r\n  position: absolute;\r\n  top: 50%;\r\n  left: 50%;\r\n  transform: translate(-50%, -50%);\r\n  text-align: center;\r\n  color: #6b7280;\r\n}\r\n\r\n.loading-spinner {\r\n  width: 40px;\r\n  height: 40px;\r\n  border: 4px solid #e5e7eb;\r\n  border-top: 4px solid #667eea;\r\n  border-radius: 50%;\r\n  animation: spin 1s linear infinite;\r\n  margin: 0 auto 16px;\r\n}\r\n\r\n@keyframes spin {\r\n  0% { transform: rotate(0deg); }\r\n  100% { transform: rotate(360deg); }\r\n}\r\n\r\n.pdf-error {\r\n  position: absolute;\r\n  top: 50%;\r\n  left: 50%;\r\n  transform: translate(-50%, -50%);\r\n  text-align: center;\r\n  color: #dc2626;\r\n  background: white;\r\n  padding: 32px;\r\n  border-radius: 12px;\r\n  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.error-icon {\r\n  font-size: 48px;\r\n  margin-bottom: 16px;\r\n}\r\n\r\n.error-close-btn {\r\n  background: #dc2626;\r\n  color: white;\r\n  border: none;\r\n  padding: 8px 16px;\r\n  border-radius: 6px;\r\n  cursor: pointer;\r\n  margin-top: 16px;\r\n  font-size: 14px;\r\n  transition: backgrou